import React from 'react';
import {graphql} from 'gatsby';
import Layout from '../components/layout';
import Image from 'gatsby-image';
import {css} from '@emotion/core';

export const query = graphql`
    query($slug: String!){
  allDatoCmsPost(filter: {slug: {eq: $slug}}) {
    nodes{
      titulo
      contenido
      imagen{
        fluid(maxWidth: 1200){
	  ...GatsbyDatoCmsFluid
        }
      }
      fecha
    }
  }
  
}
  
`;
const PostTemplate = ({data: {allDatoCmsPost:{nodes}}}) => {
      const {titulo, contenido, imagen, fecha} = nodes[0];
	return (
	  <Layout>
	      <main css={css`
		  margin: 0 auto;
		  max-width: 1200px;
		  width : 95%;
		`}>
		 <h1 css={css`
		      text-align: center;
		      margin-top: 4rem;
		      
		   `}>{titulo}</h1>
		    <h3>{fecha}</h3>
		  <p>{contenido} </p>
		<Image css={css`
		      max-height: 700px; 
		      margin-bottom: 5rem;

		  `} fluid={imagen.fluid} />	  
	      </main>
	  </Layout>
	);
}
export default PostTemplate;
